var that;
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			// navtitle: [{ id: -1, name: '平台商品' }, { id: 0, name: '商家商品' }, { id: 1, name: '我的商品' }],
			goods_list: [],
			num: 0,
			page: 1,
			nomore: false,
			nolist: false,
			idlist: [],
			maxnum:10,
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			(this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60), that.getorder();
			that.page = 1;
			that.list = [];
		},
		printl(e) {
			let index = e.currentTarget.dataset.index;
			let goods_list = that.goods_list;
			goods_list[index]['print'] = !goods_list[index]['print'];
			
			if (goods_list[index]['print']) {
				that.idlist.push(goods_list[index].id);
				console.log(that.idlist);
				that.num++;
			} else {
				var idindex = that.idlist.indexOf(goods_list[index].id);
				that.idlist.splice(idindex, 1);
				that.idlist = that.idlist;
				console.log(that.idlist);
				that.num--;
			}
			that.goods_list = goods_list;
			that.$forceUpdate();
		},
		getlist() {
			that.$request
				.request({
					url: that.api.anchor_order.choose_goods,
					method: 'GET',
					data: {
						page: that.page,
						type: 2
					}
				})
				.then(res => {
					console.log(res);
					if (res.list.length > 0) {
						that.goods_list = that.goods_list.concat(res.list);
						that.page = that.page + 1;
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.nomore = true;
							}
						} else {
							that.nolist = true;
						}
					}
				});
		},
		choosedone() {
			console.log(that.idlist);
			if (that.idlist.length == 0) {
				uni.showToast({
					title: '请选择至少一件商品',
					icon: 'none'
				});
				return false;
			}
			that.idlist = that.idlist.join();
			console.log(that.idlist);
			that.$request
				.request({
					url: that.api.anchor_order.submit_selected_goods,
					method: 'POST',
					data: {
						ids: JSON.stringify(that.idlist)
					}
				})
				.then(res => {
					console.log(res);
					uni.showToast({
						title: '提交成功！',
						icon: 'success',
						duration: 1200
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						});
					}, 1400);
				});
		}
	},
	onLoad() {
		that = this;
		that.getlist();
	    let maxnum=uni.getStorageSync("config").level_goods_num
	},
	onReachBottom() {
		that = this;
		that.getlist();
	}
};
